.modal-window-wrapper {
	width: 100vw;
	height: 100vh;
	position: fixed;
	background-color: #030303d9;
	top: 0;
	left: 0;
	z-index: 99999;

	.close-btn {
		top: 20px;
		right: 40px;
	}

	.modal-window {
		width: 100%;
		max-width: 1140px;

		.title {
			color: #ffffff;
			font-family: "Open Sans", Arial, sans-serif;
			font-size: 32px;
			font-weight: 500;
			text-align: center;
		}

		.text {
			color: #ffffff;
			font-family: "Open Sans", Arial, sans-serif;
			font-size: 16.5px;
			text-align: center;
		}
	}
}